import React, { useState } from 'react';
import {
  Container,
  Grid,
  Typography,
  Box,
  Card,
  CardContent,
  Button,
  IconButton,
  Divider,
  TextField,
  Alert,
  Paper,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import {
  Add,
  Remove,
  Delete,
  LocalShipping,
  Security,
  KeyboardArrowRight,
} from '@mui/icons-material';
import { Link as RouterLink, useNavigate } from 'react-router-dom';

// Mock cart data - in a real app, this would come from a cart context/redux store
const initialCartItems = [
  {
    id: 1,
    name: 'Premium MAGA Cap',
    price: 39.99,
    image: 'https://placehold.co/600x400/FF385C/ffffff?text=Premium+Cap',
    quantity: 1,
    size: 'One Size',
    color: 'Red',
  },
  {
    id: 2,
    name: 'Patriot Elite Polo',
    price: 59.99,
    image: 'https://placehold.co/600x400/002868/ffffff?text=Elite+Polo',
    quantity: 2,
    size: 'L',
    color: 'Navy',
  },
];

const Cart = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const navigate = useNavigate();
  const [cartItems, setCartItems] = useState(initialCartItems);
  const [promoCode, setPromoCode] = useState('');
  const [promoError, setPromoError] = useState('');
  const [promoSuccess, setPromoSuccess] = useState('');

  const updateQuantity = (itemId, change) => {
    setCartItems(items =>
      items.map(item =>
        item.id === itemId
          ? { ...item, quantity: Math.max(1, item.quantity + change) }
          : item
      )
    );
  };

  const removeItem = (itemId) => {
    setCartItems(items => items.filter(item => item.id !== itemId));
  };

  const subtotal = cartItems.reduce((sum, item) => sum + (item.price * item.quantity), 0);
  const shipping = subtotal >= 50 ? 0 : 5.99;
  const tax = subtotal * 0.08; // 8% tax
  const total = subtotal + shipping + tax;

  const handlePromoCode = () => {
    // Mock promo code validation
    if (promoCode.toLowerCase() === 'freedom') {
      setPromoSuccess('Promo code applied successfully!');
      setPromoError('');
    } else {
      setPromoError('Invalid promo code');
      setPromoSuccess('');
    }
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h4" gutterBottom>
        Shopping Cart ({cartItems.length} items)
      </Typography>

      {cartItems.length === 0 ? (
        <Card sx={{ p: 4, textAlign: 'center' }}>
          <Typography variant="h6" gutterBottom>
            Your cart is empty
          </Typography>
          <Typography color="text.secondary" paragraph>
            Looks like you haven't added any items to your cart yet.
          </Typography>
          <Button
            component={RouterLink}
            to="/categories"
            variant="contained"
            color="primary"
            sx={{ mt: 2 }}
          >
            Continue Shopping
          </Button>
        </Card>
      ) : (
        <Grid container spacing={4}>
          {/* Cart Items */}
          <Grid item xs={12} md={8}>
            <Card>
              {cartItems.map((item, index) => (
                <React.Fragment key={item.id}>
                  <CardContent>
                    <Grid container spacing={2} alignItems="center">
                      <Grid item xs={12} sm={3}>
                        <img
                          src={item.image}
                          alt={item.name}
                          style={{
                            width: '100%',
                            height: 'auto',
                            borderRadius: theme.shape.borderRadius,
                          }}
                        />
                      </Grid>
                      <Grid item xs={12} sm={9}>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                          <Typography variant="h6">{item.name}</Typography>
                          <Typography variant="h6" color="primary">
                            ${(item.price * item.quantity).toFixed(2)}
                          </Typography>
                        </Box>
                        <Typography color="text.secondary" gutterBottom>
                          Size: {item.size} | Color: {item.color}
                        </Typography>
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            <IconButton
                              size={isMobile ? "small" : "medium"}
                              onClick={() => updateQuantity(item.id, -1)}
                            >
                              <Remove />
                            </IconButton>
                            <Typography sx={{ mx: 2 }}>{item.quantity}</Typography>
                            <IconButton
                              size={isMobile ? "small" : "medium"}
                              onClick={() => updateQuantity(item.id, 1)}
                            >
                              <Add />
                            </IconButton>
                          </Box>
                          <IconButton
                            color="error"
                            size={isMobile ? "small" : "medium"}
                            onClick={() => removeItem(item.id)}
                          >
                            <Delete />
                          </IconButton>
                        </Box>
                      </Grid>
                    </Grid>
                  </CardContent>
                  {index < cartItems.length - 1 && <Divider />}
                </React.Fragment>
              ))}
            </Card>

            {/* Promo Code */}
            <Card sx={{ mt: 2, p: 2 }}>
              <Box sx={{ display: 'flex', gap: 2 }}>
                <TextField
                  size="small"
                  placeholder="Enter promo code"
                  value={promoCode}
                  onChange={(e) => setPromoCode(e.target.value)}
                  sx={{ flex: 1 }}
                />
                <Button variant="outlined" onClick={handlePromoCode}>
                  Apply
                </Button>
              </Box>
              {promoError && (
                <Alert severity="error" sx={{ mt: 1 }}>
                  {promoError}
                </Alert>
              )}
              {promoSuccess && (
                <Alert severity="success" sx={{ mt: 1 }}>
                  {promoSuccess}
                </Alert>
              )}
            </Card>
          </Grid>

          {/* Order Summary */}
          <Grid item xs={12} md={4}>
            <Paper sx={{ p: 3, position: 'sticky', top: 20 }}>
              <Typography variant="h6" gutterBottom>
                Order Summary
              </Typography>
              <Box sx={{ my: 2 }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                  <Typography>Subtotal</Typography>
                  <Typography>${subtotal.toFixed(2)}</Typography>
                </Box>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                  <Typography>Shipping</Typography>
                  <Typography>
                    {shipping === 0 ? 'Free' : `$${shipping.toFixed(2)}`}
                  </Typography>
                </Box>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                  <Typography>Tax</Typography>
                  <Typography>${tax.toFixed(2)}</Typography>
                </Box>
                <Divider sx={{ my: 2 }} />
                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                  <Typography variant="h6">Total</Typography>
                  <Typography variant="h6" color="primary">
                    ${total.toFixed(2)}
                  </Typography>
                </Box>
              </Box>
              <Button
                variant="contained"
                color="primary"
                fullWidth
                size={isMobile ? "medium" : "large"}
                endIcon={<KeyboardArrowRight />}
                onClick={() => navigate('/checkout')}
              >
                Proceed to Checkout
              </Button>

              {/* Shipping and Security Info */}
              <Box sx={{ mt: 3 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                  <LocalShipping color="action" />
                  <Typography variant="body2" color="text.secondary">
                    Free shipping on orders over $50
                  </Typography>
                </Box>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                  <Security color="action" />
                  <Typography variant="body2" color="text.secondary">
                    Secure checkout with SSL encryption
                  </Typography>
                </Box>
              </Box>
            </Paper>
          </Grid>
        </Grid>
      )}
    </Container>
  );
};

export default Cart;
